.aboutUsEmpty {
  display: flex;
  height: 208px;
  align-items: flex-start;
  align-self: stretch;
}
.aboutUsMainContainer {
  display: flex;
  align-items: flex-start;
  align-self: stretch;
}
.aboutUsMainImageContainer {
  display: flex;
  width: var(--logo-column-size);
  height: 450px;
  padding: 1px 37px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 8px;
  align-self: stretch;
  position: relative;
}
.aboutUsMainImageMark {
  position: absolute;
  width: 33px;
  right: -17px;
  opacity: 70%;
}
.aboutUsMainTextContainer {
  display: flex;
  height: 449px;
  padding: 1px 150px;
  flex-direction: column;
  justify-content: center;
  /*align-items: center;*/
  gap: 8px;
  flex: 1 0 0;
  background: var(--gold-main-accent-color);
}
.aboutUsMainTitle {
  color: #fff;
  font-size: 1.4rem;
  text-transform: uppercase;
}
.aboutUsMainText {
  color: #fff;
  font-size: 1.1rem;
}
.aboutUsMainButton {
  width: max-content;
  border: 0;
  padding: 14px;
  text-transform: uppercase;
  color: var(--blue-main-accent-color);
  background: #fff;
}
.aboutUsIconsContainer {
  display: flex;
  /*justify-content: center;
  align-items: center;*/
  align-items: flex-start;
  align-self: stretch;
}
.aboutUsIconItem {
  display: flex;
  /*height: 320px;*/
  max-width: 320px;
  /*padding: 111px 0px;*/
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex: 1 0 0;
  background: var(--gold-main-accent-color);
  text-decoration: none;
  animation: fade1In 0.5s forwards;
}
.aboutUsIconImg {
  width: 16rem;
}
.aboutUsIconText {
  text-align: center;
  color: #fff;
  text-transform: uppercase;
  margin-top: -78px;
}
.aboutUsIconItem:hover {
  /*background: var(--blue-main-accent-color);*/
  animation: fadeIn 1s forwards;
}
@keyframes fadeIn {
  0% {
    background: var(--gold-main-accent-color);
  }
  100% {
    background: var(--blue-main-accent-color);
  }
}

@keyframes fade1In {
  0% {
    background: var(--blue-main-accent-color);
  }
  100% {
    background: var(--gold-main-accent-color);
  }
}
@media screen and (min-width: 992px) and (max-width: 1352px) {
  .aboutUsMainTextContainer {
    padding: 1px 45px;
  }
  .aboutUsIconItem {
    width: 20%;
    height: 12.7rem;
  }
  .aboutUsIconText {
    margin-top: -98px;
    font-size: small;
  }
  .aboutUsIconItem:active {
    /*background: var(--blue-main-accent-color);*/
    animation: fadeIn 0.5s;
  }
}
@media screen and (max-width: 991px) {
  .aboutUsEmpty {
    height: 104px;
  }
  .aboutUsMainContainer {
    flex-direction: column;
  }
  .aboutUsMainImageContainer {
    width: 100%;
    height: 225px;
    padding: 1px 0px;
  }
  .aboutUsMainImageMark {
    display: none;
  }
  .aboutUsMainTextContainer {
    padding: 18px 45px;
  }
  .aboutUsIconItem {
    width: 20%;
    height: 7rem;
  }
  .aboutUsIconText {
    margin-top: -54px;
    font-size: 6px;
  }
  .aboutUsIconItem:active {
    /*background: var(--blue-main-accent-color);*/
    animation: fadeIn 0.5s;
  }
  .aboutUsMainTitle {
    font-size: 30px;
  }
  .aboutUsMainText {
    font-size: 16px;
  }

  element.style {
  }
  .aboutUsIconImg {
    width: 10rem;
  }
}
