.img {
  /*background-color: #092f63;*/
  height: 6rem;
}
.headerLogo {
  height: 3rem;
  margin: 0 1rem;
}
.app-navbar {
  top: 9rem;
}
.visibled {
  display: block;
}

.dropdown-item {
  text-transform: uppercase;
  color: #570723;
}
/*.nav-link {
  text-transform: uppercase;
  color: #570723;
}*/
.show {
  display: block;
  position: absolute;
  right: 1rem;
  z-index: 1;
  background: #ffffff;
}
.dropdown-toggle {
  border: none;
  background: transparent;
}
.nav-item a {
  text-decoration: none;
  color: #092f63;
}
.nav-link {
  text-transform: uppercase;
  color: #092f63;
  font-size: 18px;
}
.lang-select {
  text-transform: uppercase;
}

/*.lang-select label {
  display: none;
}*/
.lang-dropdown {
  text-transform: uppercase;
  color: #092f63;
}

/*--------------------------*/
.containerNavbar {
  display: flex;
  height: 200px;
  padding: 8px 26px 8px 0px;
  justify-content: center;
  align-items: center;
  gap: 8px;
  flex-shrink: 0;
  align-self: stretch;
}
.containerLogo {
  display: flex;
  background: var(--blue-main-accent-color);
  /*height: 200px;*/
  /*width: var(--logo-column-size);*/
  padding: 66px 89px;
  flex-direction: column;
  align-items: flex-start;
  gap: 8px;
}
.containerMenu {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  list-style: none;
}
.containerMenu a {
  text-decoration: none;
}
.nav-link {
  display: flex;
  padding: 8px 16px;
  justify-content: center;
  align-items: center;
  gap: 8px;
}
.active {
  color: #cc9653;
}
.spacer {
  height: 40px;
  flex: 1 0 0;
}
.logo {
  width: 221.729px;
  height: 95.089px;
  flex-shrink: 0;
}
.dropbtn {
  background-color: var(--white-bg-color);
  color: var(--blue-main-accent-color);
  padding: 16px;
  font-size: 16px;
  border: none;
  cursor: pointer;
  text-transform: uppercase;
}

.dropdown {
  position: relative;
  display: inline-block;
  padding: 8px 16px;
  justify-content: center;
  align-items: center;
  gap: 8px;
}
.dropdownMenu {
  position: relative;
  display: inline-block;
  padding: 8px 16px;
  justify-content: center;
  align-items: center;
  gap: 8px;
}
.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 60px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}
.dropdown-contentMenu {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 60px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}
.dropdown-content button {
  color: var(--blue-main-accent-color);
  padding: 12px 16px;
  text-decoration: none;
  text-transform: uppercase;
  display: block;
  background-color: #f9f9f9;
  border: 0;
  width: 100%;
}
.dropdown-contentMenu button {
  color: var(--blue-main-accent-color);
  padding: 12px 16px;
  text-decoration: none;
  text-transform: uppercase;
  display: block;
  background-color: #f9f9f9;
  border: 0;
  width: 100%;
}
.dropdown-content button:hover {
  background-color: var(--white-bg-color);
}
.dropdown-contentMenu button:hover {
  background-color: var(--white-bg-color);
}

.dropdown:hover .dropdown-content {
  display: block;
}
.dropdownMenu:hover .dropdown-contentMenu {
  display: block;
}
.dropdown:hover .dropbtn {
  background-color: var(--white-bg-color);
}
.dropdownMenu:hover .dropbtn {
  background-color: var(--white-bg-color);
}
.iconLang {
  position: absolute;
  margin-left: 0.3rem;
}
.containerNavbarTablet {
  display: none;
  height: 200px;
  padding: 8px 0px;
  justify-content: center;
  align-items: center;
  gap: 8px;
  flex-shrink: 0;
  align-self: stretch;
}
.containerLogoTablet {
  display: flex;
  height: 100px;
  padding: 66px 89px;
  flex-direction: column;
  align-items: flex-start;
  gap: 8px;
}
.logoTablet {
  width: 221.729px;
  height: 95.089px;
  flex-shrink: 0;
}
.spacerTablet {
  height: 40px;
  flex: 1 0 0;
}
.menuMobileContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
}

.menuButtonMobile {
  display: flex;
  padding: 8px 16px;
  justify-content: center;
  align-items: center;
  gap: 8px;
  border: 0;
  background: var(--white-bg-color);
}

@media screen and (min-width: 992px) and (max-width: 1352px) {
  .containerNavbar {
    display: none;
  }
  .containerNavbarTablet {
    display: flex;
  }
  .dropdownMenu:hover .dropdown-contentMenu {
    margin-top: 21rem;
  }
}
@media screen and (max-width: 991px) {
  .containerNavbar {
    display: none;
  }
  .containerNavbarTablet {
    display: flex;
    height: 128px;
    padding: 8px 18px;
    justify-content: center;
    align-items: center;
    gap: 8px;
    flex-shrink: 0;
    align-self: stretch;
  }
  .containerLogoTablet {
    height: auto;
    padding: 0;
  }
  .logoTablet {
    width: 129.519px;
    height: 55.544px;
  }
  .menuIconMobile {
    width: 1.5rem;
  }
  .dropdownMenu {
    padding: 0;
  }
  .dropdown {
    padding: 0;
  }
  .dropdownMenu:hover .dropdown-contentMenu {
    margin: 21rem 7rem 0 0;
  }
}
@media screen and (min-width: 1588px) and (max-width: 1645px) {
