.socialContainer {
  display: flex;
  width: 120px;
  padding: 8px 0 8px 8px;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  right: 0px;
  top: 300px;
  background: rgba(241, 242, 245, 0.4);
  border-radius: 50% 0px 0px 50% /20%;
}
.socialButton {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 34px;
}
.socialIcon {
  width: 47px;
  height: 47px;
  border: 0;
  background: transparent /*var(--white-bg-color)*/;
}
.socialMask {
  width: 61px;
  height: 433px;
}
.animateIn {
  transform: translateX(100%);
  animation: slideIn 1s forwards;
}
@keyframes slideIn {
  to {
    transform: translateX(0);
  }
}
.animateOut {
  transform: translateX(0);
  animation: slideOut 1s forwards;
}
@keyframes slideOut {
  to {
    transform: translateX(100%);
  }
}
@media screen and (max-width: 991px) {
  .socialContainer {
    top: 152px;
    width: 92px;
    height: 21rem;
  }
  .socialMask {
    width: 30px;
  }
  .socialIcon {
    width: 30px;
    height: 30px;
  }
}
