.container {
  display: flex;
  /*width: 1427px;
  height: 2130px;*/
  padding: 0px 148px;
  flex-direction: column;
  align-items: center;
  position: relative;
  overflow: hidden;
}
.footerApp {
  display: flex;
  /*position: absolute;
  bottom: 0;*/
  height: 200px;
  background: var(--blue-main-accent-color);
}
@media screen and (min-width: 992px) and (max-width: 1352px) {
  .container {
    padding: 0px 86px 0px 80px;
  }
}
@media screen and (max-width: 991px) {
  .container {
    padding: 0px;
  }
}
