.video {
  width: 100%;
}
/*----*/
.heroContainer {
  display: flex;
  align-items: flex-start;
  align-self: stretch;
}
.heroDescContainer {
  display: flex;
  width: var(--logo-column-size);
  padding: 0px 37px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 31px;
  align-self: stretch;
  background: var(--blue-main-accent-color);
}
.heroTitle {
  color: #fff;
  font-size: 2rem;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-transform: uppercase;
}
.heroDesc {
  color: #fff;
  font-size: 1.2rem;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-transform: uppercase;
}
.heroVideoContainer {
  height: 587px;
  flex: 1 0 0;
}
.heroVideoLabel {
  display: flex;
  height: 208px;
  padding: 1px 200px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 8px;
  flex: 1 0 0;
}
.heroVideoButton {
  display: flex;
  padding: 8px 16px;
  justify-content: center;
  align-items: center;
  gap: 8px;
  border: 0;
  color: var(--blue-main-accent-color);
  font-size: large;
  text-transform: uppercase;
  cursor: pointer;
  background: var(--white-bg-color);
}
.heroActionBlock {
  display: flex;
  align-items: flex-start;
  align-self: stretch;
}
.heroDescLabel {
  display: flex;
  position: relative;
  width: var(--logo-column-size);
  padding: 1px 37px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 8px;
  align-self: stretch;
  background: var(--blue-main-accent-color);
}
.heroDescMarkGroup {
  /*width: 61px;*/
  height: 311px;
  transform: rotate(-90deg);
  position: absolute;
  left: 185px;
  bottom: -156px;
}
.heroDescLabelButton {
  background: inherit;
  border: 0;
  color: #fff;
  font-size: large;
  text-transform: uppercase;
  cursor: pointer;
}
.heroLabelMark {
  height: 1.2rem;
  vertical-align: text-top;
}
@media screen and (min-width: 992px) and (max-width: 1352px) {
  .heroContainer {
    flex-direction: column;
    align-items: center;
  }
  .heroDescContainer {
    padding: 33px 66px;
    gap: 21px;
    width: auto;
  }
  .heroVideoContainer {
    height: 516px;
    align-self: stretch;
    flex: auto;
  }
  .heroDescLabel {
    display: flex;
    width: 11rem;
    padding: 1px 114px;
  }
  .heroDescLabelButton {
    width: max-content;
  }
  .heroVideoLabel {
    padding: 1px 114px;
  }
  .heroVideoButton {
    width: max-content;
  }
}
@media screen and (max-width: 991px) {
  .heroContainer {
    flex-direction: column;
    align-items: center;
  }
  .heroDescContainer {
    padding: 17px 18px;
    gap: 21px;
    height: fit-content;
    width: auto;
  }
  .heroTitle {
    font-size: 33px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
  }
  .heroDesc {
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
  }
  .heroVideoContainer {
    height: 266px;
    align-self: stretch;
    flex: auto;
  }
  .heroDescLabel {
    display: flex;
    width: 50%;
    height: 75px;
    padding: 1px 11px;
    flex: 1 0 0;
  }
  .heroDescLabelButton {
    width: max-content;
    font-size: 17px;
  }

  .heroVideoLabel {
    width: 50%;
    height: 75px;
    padding: 1px 5px;
    width: max-content;
    flex: 1 0 0;
  }
  .heroVideoButton {
    width: max-content;
    font-size: 17px;
  }
  .heroDescMarkGroup {
    display: none;
  }
}
